/*
 * Base structure
 */

.input{
     outline: none !important;
}


 /* Move down content because we have a fixed navbar that is 50px tall */
 body {

   margin: 0;
   padding: 0;
   background-image: url('../img/halftoneDark.png');

 }

 .piece {
  background-image: url('../img/halftone.png');
}

.imgGD {
  background-image: url('../img/halftone.png'); 
}



.pannelClose{
  margin-bottom: -25px;
  z-index: 1025;
  position: relative;
  text-align: center;
  color: white;

  i {
    font-size: 2em;
    margin: 0px
  }

  button{
    height: 50px;
    width: 50px;
    margin: 0 auto;
    border: 3px solid  @hydro-braun;
    -webkit-border-radius: 60;
    -moz-border-radius: 60;
    border-radius: 60px;
    font-family: Arial;
    color: #ffffff;
    font-size: 10px;
    background: @hydro-braun-light;
    text-decoration: none;
    &:hover {
      background: @hydro-blue-middle;
      text-decoration: none;
    }
  }
}


/*
* Table Dashborad E
*/

.dash_experience {
  th{
    font-size: 1.4em;
    color: @hydro-black;
    text-align: right;
  }
  td{
    font-size: 1.4em;
    color: @hydro-black;
    margin-right: 40%;

  }
}


.dash_influence{
  width: 50%;
  float: left;
  text-align: center;
  font-size: 1.5em;
  .circle_1{
    height: 80px;
    position: relative;
    strong{
	    	font-size: 1.5em;
		    text-align: center;
	    i{
			position: absolute;
			left: 0;
			width: 100%;
			height: 100%;
			top: 22%;
	    }
	}
  }

  .influence {
    h1{
      
      margin-top: 5px;
      font-family: @font-family-sans-serif;
    }

    
  }
}
.dash_secteurs{
  width: 50%;
  float: left;
  text-align: center;
  font-size: 1.5em;
  .secteur {
    font-family: @font-family-sans-serif;
  }

  .secteur_qte{
    height: 80px;
    position: relative;
    p{
	    position: absolute;
	    font-size: 1.8em;
	    width: 100%;
		top: 22%;
		margin: 0;
	    left: 0;
		i {
		  color: @hydro-blue-light;
		}	    
    }
  }
  .secteur_titre{
    h1{
      margin-top: 5px;
      font-family: @font-family-sans-serif;
    }

  }
}








.contentDashboard {

}

.contentDashboardRow {
  margin-bottom: 15px;
  margin-top: 15px;
  border-bottom: solid 2px @hydro-braun-light;
  padding-top: 15px;
  padding-bottom: 15px;

}





/*
* Icones Actionlist
*/

.icon-actionlist {
  font-size: 3.4em;
  color: @hydro-braun-light;

  &:hover {
    color:@hydro-blue-middle;
    cursor: pointer;
  }
}
.active{
  color: @hydro-blue;
}


/*
* Bottom Cotent
*/
.bottom {
  background-color: red;
  bottom: 0;
  position: absolute;
  height: 60px;
}

.mainDaschboard{
  min-height: 200px;
  background-image: url('../img/halftone.png');
  border-top: solid 5px @hydro-braun;
  h1 { 
    margin-top: 0px;
  }
}





/*
 * Global add-ons
 */

 .sub-header {
  padding-bottom: 10px;
  border-bottom: 1px solid #eee;
}

/*
 * Top navigation
 * Hide default border to remove 1px line.
 */
 .navbar-fixed-top {
  border: 0;
}

/*
 * Sidebar
 */

 /* Hide for mobile, show later */
 .sidebar {
  display: none;
}


@media (min-width: 768px) {
  .sidebar {
    position: fixed;
    top: 51px;
    bottom: 0;
    left: 0;
    z-index: 1000;
    display: block;
    padding: 20px;
    overflow-x: hidden;
    overflow-y: auto; /* Scrollable contents if viewport is shorter than content. */
    background-color: #f5f5f5;
    border-right: 1px solid #eee;
  }
}


/*
 * Main content
 */

 .main {
  padding-left: 20px;
}


@media (min-width: 768px) {
  .main {
    padding-right: 40px;
    padding-left: 40px;
  }
}
.main .page-header {
  margin-top: 0;
}


/*
 * Placeholder dashboard ideas
 */

 .placeholders {
  margin-bottom: 30px;
  text-align: center;
}
.placeholders h4 {
  margin-bottom: 0;
}
.placeholder {
  margin-bottom: 20px;
}
.placeholder img {
  display: inline-block;
  border-radius: 50%;
}